h1 {
    font-size: xx-large;
    color: #c1c110;
    text-align: center;
}

.projectMain {
    width: 95%;
    margin: auto;
    display: grid;
    justify-content: space-around;
    grid-template-columns: repeat(2, 45%);
    column-gap: 0.8rem;
    margin-bottom: 2rem;
}

.projectItem {
    background-color: rgb(30, 32, 32);
    height: 600px;
    /* width: 40vw; */
    border: 3px solid rgb(224, 232, 127);
    border-radius: 0.5rem;
    margin: auto;
    margin-bottom: 3rem;

}

.projectItem:hover{
    transition:.6s;
    box-shadow: 0px 0px 20px rgb(255, 255, 255);
  }

.projectItem h2{
    text-decoration: underline;
    color:yellow
}

.tmetricimg {
    width: 100%;
    border-radius: 0.5rem 0.5rem 0 0;
    height: 40%;

}

.projectItem h3 {
    margin: 1rem;
}


.projectBtn{
    margin: auto;
    display: flex;
    justify-content: space-evenly;
}

.btnweb {
    /* margin: 1rem; */
    padding: 10px;
    /* margin:auto; */
    font-size: large;
    color: rgb(255, 255, 255);
    border-radius: 5px;
    background-color: #000000;
    cursor: pointer;
    position: relative;
    z-index: 0;
    border: none;
    outline: none;
}

.btngit {
    /* margin: 1rem; */
    padding: 10px;
    /* margin-left: 3rem; */
    font-size: large;
    color: rgb(255, 255, 255);
    border-radius: 5px;
    background-color: #000000;
    cursor: pointer;
    position: relative;
    z-index: 0;
    border: none;
    outline: none;
}

.btngit:before {
    content: '';
    background: linear-gradient(45deg, #ff0000, #ff7300, #fffb00, #48ff00, #00ffd5, #002bff, #7a00ff, #ff00c8, #ff0000);
    position: absolute;
    top: -2px;
    left: -2px;
    background-size: 400%;
    z-index: -1;
    filter: blur(5px);
    width: calc(100% + 4px);
    height: calc(100% + 4px);
    animation: glowing 20s linear infinite;
    opacity: 0;
    transition: opacity .3s ease-in-out;
    border-radius: 10px;
}

.btngit:active {
    color: #000
}

.btngit:active:after {
    background: transparent;
}

.btngit:hover:before {
    opacity: 1;
}

.btngit:after {
    z-index: -1;
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    background: #000000;
    left: 0;
    top: 0;
    border-radius: 10px;
}

@keyframes glowing {
    0% {
        background-position: 0 0;
    }

    50% {
        background-position: 400% 0;
    }

    100% {
        background-position: 0 0;
    }
}

