@media (max-width:1500px) {
    .projectItem h3 {
        font-size: 17px;
        height: auto;
    }
}

@media (max-width:948px) {
    
    
    .tmetricimg {
        width: 100%;
        border-radius: 0.5rem 0.5rem 0 0;
        height: 30%;
    
    }
    
}

@media (max-width:880px) {
    .projectItem h3 {
        font-size: 18px;
    }
}


@media (max-width:750px) {
    .projectMain {
        display: grid;
        grid-template-columns: repeat(1, 1fr);
        margin: auto;

    }
    .projectItem {
        background-color: rgb(30, 32, 32);
        height: 35rem;
        width: 75vw;
        border: 3px solid rgb(224, 232, 127);
        border-radius: 0.5rem;
        margin-bottom: 3rem;
        /* margin: 0%; */
        box-shadow: rgb(0, 0, 0) 0px 5px 15px;
    
    
    }
    .tmetricimg {
        width: 100%;
        border-radius: 0.5rem 0.5rem 0 0;
        height: 40%;
    
    }
    .projectItem h3 {
        font-size: 17px;
    }
   
}

@media (max-width:515px) {
    .projectItem h3 {
        margin: 0.5rem;
        font-size: 17px;
    }
    .projectMain{
        margin-left: 0.5rem;
        /* margin-right: 0.1rem; */
    }
}

@media (max-width:450px) {
    .projectItem h3 {
        font-size: 14px;
    }
}

@media (max-width:370px) {
    .projectItem h3 {
        margin: 0.5rem;
        font-size: 13px;
    }
}