#intro {
    width: 85%;
    height: auto;
    margin: 20px auto;
    padding: 1rem;
}

.introContent {
    width: 100%;
    margin-top: auto;
    margin-bottom: auto;
    display: flex;
    justify-content: space-between;
}

.introTextContentDiv {
    width: 50%;
    margin: auto 0px;
    justify-content: center;
    align-items: center;

}

.introProfileImgDiv {
    width: auto;
}

