.photo {
    width: auto;
    height: 450px;
    border-radius: 50%;
    border: 3px solid rgb(210, 179, 245);
    box-shadow: rgb(0, 0, 0) 0px 30px 20px;
}


.hello {
    font-size: 1.75rem;
    font-weight: 100;
}

.helloimg {
    height: 50px;
    width: 50px;
}

.introText {
    font-size: 3rem;
    font-weight: bold;
}

.introName {
    color: yellow;
}

